@import "../../global.scss";

.topbar {
    width:100%;
    height:70px;
    background-color: white;
    color: $mainColor;
    position: fixed;
    top: 0;
//makes sure topbar is always fixed when page is bigger than other components, 
//changed z-index from 2 to 3 once menu was added
    z-index: 3;
    //helps w/ color transtion in hamburger menu
    transition: all 1s ease ;

    .wrapper{
        padding: 10px 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .left{
            display: flex;
            align-items: center;


            .logo{
                font-size: 40px;
                font-weight: 700;
                //gets rid of anchor decorations (color, underline)
                text-decoration: none;
                //will inherit color from parent, the dark blue on line 7 coming from global
                color: inherit;
                margin-right: 40px;
            }

            .itemContainer{
                display: flex;
                align-items: center;
                margin-left: 30px;

                @include mobile{
                    display: none;
                }

                .icon{
                    font-size: 18px;
                    margin-right: 5px;
                }

                span{
                    font-size: 15px;
                    font-weight: 500;
                }
            }
        }

        .right{

            .hamburger{
                width: 32px;
                height: 25px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                cursor: pointer;

                span{
                    width: 100%; //the entire 32px
                    height: 3px;
                    background-color: $mainColor;
                    transform-origin: left; //so that when it's active it can rotate left, not from default center
                    transition: all 2s ease; //transition of the x
                
                }
            }
        }

    }
//and if there's active
    &.active{
            background-color: $mainColor;
            color:white;

            .hamburger{
                span{
                    &:first-child{ //first child, meaning first line
                        background-color: white;
                        transform: rotate(45deg);
                    }
                    &:nth-child(2){ //second line, dont want to see it
                        opacity: 0;
                    }
                    &:last-child{ //last line
                        background-color: white;
                        transform: rotate(-45deg);
                    }
                }
            }
    }

}